'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Slider } from '@/components/ui/slider';
import { Play } from 'lucide-react';
import type { MindfulnessSessionType, ExperienceLevel } from '@/lib/types';

interface SessionSelectorProps {
  sessionType: MindfulnessSessionType;
  onStartSession: (type: MindfulnessSessionType, duration: number, experience: ExperienceLevel) => void;
}

export function SessionSelector({ sessionType, onStartSession }: SessionSelectorProps) {
  const [duration, setDuration] = useState([5]);
  const [experience, setExperience] = useState<ExperienceLevel>('beginner');

  const handleStart = () => {
    onStartSession(sessionType, duration[0], experience);
  };

  const getDurationLabel = (minutes: number) => {
    if (minutes === 1) return '1 minute';
    return `${minutes} minutes`;
  };

  const getRecommendedDuration = () => {
    switch (sessionType) {
      case 'mindful-moment':
        return [1, 5];
      case 'breathing':
        return [3, 10];
      case 'meditation':
        return [5, 20];
      case 'body-scan':
        return [10, 25];
      default:
        return [3, 15];
    }
  };

  const [minDuration, maxDuration] = getRecommendedDuration();

  return (
    <div className="space-y-4">
      <div>
        <label className="text-sm font-medium mb-2 block">Duration</label>
        <div className="px-2">
          <Slider
            value={duration}
            onValueChange={setDuration}
            min={minDuration}
            max={maxDuration}
            step={1}
            className="mb-2"
          />
          <div className="flex justify-between text-xs text-muted-foreground">
            <span>{getDurationLabel(minDuration)}</span>
            <span className="font-medium">{getDurationLabel(duration[0])}</span>
            <span>{getDurationLabel(maxDuration)}</span>
          </div>
        </div>
      </div>

      <div>
        <label className="text-sm font-medium mb-2 block">Experience Level</label>
        <Select value={experience} onValueChange={(value: ExperienceLevel) => setExperience(value)}>
          <SelectTrigger>
            <SelectValue />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="beginner">Beginner</SelectItem>
            <SelectItem value="intermediate">Intermediate</SelectItem>
            <SelectItem value="advanced">Advanced</SelectItem>
          </SelectContent>
        </Select>
      </div>

      <Button onClick={handleStart} className="w-full" size="lg">
        <Play className="w-4 h-4 mr-2" />
        Start Session
      </Button>
    </div>
  );
}